import './About.css';
import mainPhoto from '../../images/about-photo-1.png';
import photo1 from '../../images/about-photo-2.png';
import photo2 from '../../images/about-photo-3.png';
import photo3 from '../../images/about-photo-4.png';
import React from 'react';
import Fade from 'react-reveal/Fade';

function About() {
  const [isPopupOpened, setIsPopupOpened] = React.useState(false);
  const [popupAlt, setPopupAlt] = React.useState('');
  const [popupSrc, setPopupSrc] = React.useState(mainPhoto);

  function handlePopupOpen(alt, src) {
    setPopupAlt(alt);
    setPopupSrc(src)
    setIsPopupOpened(!isPopupOpened);
  }
  return (
    <section className='about' name="about" id="about">
      <div className={`about__overlay ${isPopupOpened ? 'about__overlay_visible' : ''}`}></div>
      <div className={`about__popup ${isPopupOpened ? 'about__popup_visible' : ''}`}>
        <img alt={popupAlt} src={popupSrc} className='about__popup-img'></img>
        <button type="button" onClick={() =>  setIsPopupOpened(false)} className='about__popup-close'></button>
      </div>
      <Fade top>
        <h2 className='about__title'> </h2>
      </Fade>
      <div className='about__images'>
        <Fade left>
          <img alt="  " src={mainPhoto} onClick={() => handlePopupOpen("  ", mainPhoto)} className="about__image_main about__image_main-desktop"></img>
        </Fade>
        <Fade left>
          <img alt="  " src={mainPhoto} className="about__image_main about__image_main-mobile"></img>
        </Fade>
        <Fade top={window.innerWidth > 900 ? true : false} bottom={window.innerWidth > 900 ? false : true}>
          <div className='about__info'>
            <div className='about__info-row'>
              <div className='about__info-icon'></div>
              <p className='about__info-text'>   ,    </p>
            </div>
            <div className='about__info-row'>
              <div className='about__info-icon'></div>
              <p className='about__info-text'>        </p>
            </div>
            <div className='about__info-row'>
              <div className='about__info-icon'></div>
              <p className='about__info-text'>       , 
                ,    </p>
            </div>
            <div className='about__info-row'>
              <div className='about__info-icon'></div>
              <p className='about__info-text'>       , 
                  </p>
            </div>
            <div className='about__info-row'>
              <div className='about__info-icon'></div>
              <p className='about__info-text'>    ,  , ,  
                 </p>
            </div>
            <div className='about__info-row'>
              <div className='about__info-icon'></div>
              <p className='about__info-text'>     ,      </p>
            </div>
          </div>
        </Fade>
        <Fade bottom>
          <img alt=" " src={photo1} onClick={() => handlePopupOpen(" ", photo1)} className='about__image about__image_desktop'></img>
          <img alt=" " src={photo2} onClick={() => handlePopupOpen(" ", photo2)} className='about__image about__image_desktop'></img>
          <img alt=" " src={photo3} onClick={() => handlePopupOpen(" ", photo3)} className='about__image about__image_desktop'></img>
        </Fade>
        <Fade bottom>
          <div className='about__image-list_mobile'>
            <img alt=" " src={photo1} className='about__image'></img>
            <img alt=" " src={photo2} className='about__image'></img>
            <img alt=" " src={photo3} className='about__image'></img>
          </div>
        </Fade>
      </div>
      <Fade bottom>
        <div className='about__numbers'>
          <div className='about__numbers-block'>
            <p className='about__numbers-text'><span className='about__numbers-text_bold'>19</span>  <br /> </p>
          </div>
          <div className='about__numbers-block'>
            <p className='about__numbers-text'><span className='about__numbers-text_bold'>21 900</span><br /><br /></p>
          </div>
          <div className='about__numbers-block'>
            <p className='about__numbers-text'><span className='about__numbers-text_bold'>2 700</span>  </p>
          </div>
          <div className='about__numbers-block'>
            <p className='about__numbers-text'><span className='about__numbers-text_bold'>90 %</span>     </p>
          </div>
        </div>
      </Fade>
      
    </section>
  );
}

export default About;
